import { createSlice } from '@reduxjs/toolkit';
import { getTokenWithEmail, getTokenWithPhone, getUserData } from './middleware';

type initialStateType = {
	token: string | null;
	id: number;
	firstName: string;
	lastName: string;
	middleName: string;
	email: string;
	phone: string;
	birthday: string;
	city: string;
	subscribeNewsletter: boolean;
};

const initialState: initialStateType = {
	token: null,
	id: null,
	firstName: null,
	lastName: null,
	middleName: null,
	email: null,
	phone: null,
	birthday: null,
	city: null,
	subscribeNewsletter: false,
};

const UserDataStore = createSlice({
	name: 'userData',
	initialState,
	reducers: {
		changeToken(state, action) {
			state.token = action.payload;
		},
		changeId(state, action) {
			state.id = action.payload;
		},
		changeFirstName(state, action) {
			state.firstName = action.payload;
		},
		changeLastName(state, action) {
			state.lastName = action.payload;
		},
		changeMiddleName(state, action) {
			state.middleName = action.payload;
		},
		changeEmail(state, action) {
			state.email = action.payload;
		},
		changePhone(state, action) {
			state.phone = action.payload;
		},
		changeBirthday(state, action) {
			state.birthday = action.payload;
		},
		changeCity(state, action) {
			state.city = action.payload;
		},
		changeSubscribeNewsletter(state, action) {
			state.subscribeNewsletter = action.payload;
		},
	},
	extraReducers(builder) {
		builder.addCase(getTokenWithEmail.fulfilled, (state, action) => {
			state.token = action.payload.data.token;
		});
		builder.addCase(getTokenWithPhone.fulfilled, (state, action) => {
			state.token = action.payload.data.token;
		});
		builder.addCase(getUserData.fulfilled, (state, action) => {
			state.id = action.payload.data.id;
			state.firstName = action.payload.data.firstName;
			state.lastName = action.payload.data.lastName;
			state.middleName = action.payload.data.middleName;
			state.email = action.payload.data.email;
			state.phone = action.payload.data.phone;
			state.birthday = action.payload.data.birthday;
			state.city = action.payload.data.city;
			state.subscribeNewsletter = action.payload.data.subscribeNewsletter;
		});
	},
});

export const {
	changeToken,
	changeId,
	changeFirstName,
	changeEmail,
	changePhone,
	changeBirthday,
	changeCity,
	changeSubscribeNewsletter,
	changeLastName,
	changeMiddleName,
} = UserDataStore.actions;

export default UserDataStore.reducer;
