type HeaderItemDataApi = {
	name: string;
	code: string;
	id: number;
	childs: HeaderItemDataApi[] | [];
};

export type HeaderItemCategoriesModel = {
	name: string;
	code: string;
};

export type HeaderSectionItemModel = {
	name: string;
	href: string;
};

export type HeaderSectionModel = Record<string, HeaderSectionItemModel[]>;

export type HeaderItemDataModel = {
	name: string;
	code: string;
	categories: HeaderItemCategoriesModel[] | [];
	section: Record<string, HeaderSectionModel[]> | [];
	banner?: {
		bannerImg: string;
		text: string;
		link: string;
	};
};

export type HeaderDataApi = {
	data: HeaderItemDataApi[];
};

export type HeaderDataModel = HeaderItemDataModel[];

export const normolizeHeaderData = (item: HeaderDataApi): HeaderDataModel => {
	return item.data.map(itemRes => {
		return {
			name: itemRes.name,
			code: itemRes.code,
			categories: itemRes.childs
				.map(itemCateg => {
					if (itemCateg.childs.length === 0) {
						return { name: itemCateg.name, code: itemCateg.code };
					}
					return null;
				})
				.filter(filtredItem => filtredItem !== null),
			section: {
				...Object.assign(
					{},
					...itemRes.childs
						.map((itemSection, index) => {
							if (itemSection.childs.length !== 0) {
								return {
									[Object.values(itemRes.childs).map(item => item.name + ':' + item.code)[index]]:
										itemSection.childs.map(itemSectionList => {
											return { name: itemSectionList.name, href: itemSectionList.code };
										}),
								};
							}
							return null;
						})
						.filter(filtredItem => filtredItem !== null)
				),
			},
		};
	});
};
