import { normalizeOrderListArr, OrderListModel } from 'api/order/models/orderList';
import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, OrderListApiParams, IOrderListApi } from './types';

export default class OrderListApi implements IOrderListApi {
	private readonly _apiStore = RootStore.apiStore;

	async getOrderList(params: OrderListApiParams): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<OrderListModel, OrderListModel>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/order?page=${params.page}&pageSize=${params.pageSize}`,
		});
		try {
			response.data = normalizeOrderListArr(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
