import { createSlice } from '@reduxjs/toolkit';
import { CatalogItemDataModel } from 'api/catalog/models/CatalogListModel';
import { getCatalogList, getFilterList } from './middleware';

type initialStateType = {
	catalogList: CatalogItemDataModel[];
	totalPages: number;
	totalCounter: number | null;
	currentPage: number;
	catalogIsLoaded: boolean;
	categories: number[];
	sizes: number[];
	colors: number[];
	categoriesList: {
		id: number;
		name: string;
	}[];
	sizesList: {
		id: number;
		name: string;
	}[];
	colorsList: {
		id: number;
		name: string;
	}[];
	sortParam: string;
	sortType: string;
};

const initialState: initialStateType = {
	catalogList: [],
	totalPages: 3,
	totalCounter: null,
	currentPage: 2,
	catalogIsLoaded: true,
	categories: [],
	sizes: [],
	colors: [],
	categoriesList: [],
	sizesList: [],
	colorsList: [],
	sortParam: 'date',
	sortType: 'asc',
};

const CatalogListStore = createSlice({
	name: 'catalog',
	initialState,
	reducers: {
		changeCatalogList(state, action) {
			state.catalogList = action.payload.catalogList;
		},
		changeCurrentPage(state, action) {
			state.currentPage = action.payload;
		},
		changeTotalPages(state, action) {
			state.totalPages = action.payload;
		},
		changeTotalCounter(state, action) {
			state.totalCounter = action.payload;
		},
		changeCategories(state, action) {
			state.categories = action.payload;
		},
		changeSizes(state, action) {
			state.sizes = action.payload;
		},
		changeColors(state, action) {
			state.colors = action.payload;
		},
		changeCategoriesList(state, action) {
			state.categoriesList = action.payload;
		},
		changeSizesList(state, action) {
			state.sizesList = action.payload;
		},
		changeColorsList(state, action) {
			state.colorsList = action.payload;
		},
		changeSortParam(state, action) {
			state.sortParam = action.payload.sortParam;
		},
		changeSortType(state, action) {
			state.sortType = action.payload.sortType;
		},
	},
	extraReducers(builder) {
		builder
			.addCase(getCatalogList.pending, (state, action) => {
				state.catalogIsLoaded = false;
			})
			.addCase(getCatalogList.fulfilled, (state, action) => {
				state.catalogList = Array.isArray(state.catalogList)
					? state.catalogList.concat(action.payload.data.data)
					: action.payload.data.data;
				state.totalPages = action.payload.data.totalPages;
				state.totalCounter = action.payload.data.totalCounter;
				state.catalogIsLoaded = true;
				state.currentPage += 1;
			})
			.addCase(getFilterList.fulfilled, (state, action) => {
				state.categoriesList = action.payload.data.categories;
				state.colorsList = action.payload.data.colors;
				state.sizesList = action.payload.data.dimensions;
			});
	},
});

export const {
	changeCatalogList,
	changeCurrentPage,
	changeTotalPages,
	changeTotalCounter,
	changeCategories,
	changeSizes,
	changeColors,
	changeCategoriesList,
	changeSizesList,
	changeColorsList,
	changeSortParam,
	changeSortType,
} = CatalogListStore.actions;

export default CatalogListStore.reducer;
