export type ReviewDataApi = {
	data: {
		id: number;
		author: {
			id: number;
			firstName: string;
			lastName: string;
			middleName: string;
			email: string;
		};
		rating: number;
		message: string;
	}[];
	total_counter: number;
	total_rating: number;
	total_pages: number;
};

export type ReviewDataModel = {
	counter: number;
	totalStars: number;
	totalPages: number;
	reviewsList: {
		id: number;
		name: string;
		text: string;
		stars: number;
	}[];
};

export const normolizeReviewData = (item: ReviewDataApi): ReviewDataModel => {
	return {
		counter: item.total_counter ? item.total_counter : null,
		totalStars: item.total_rating ? item.total_rating : null,
		totalPages: item.total_pages ? item.total_pages : null,
		reviewsList:
			Array.isArray(item.data) &&
			item.data.length !== 0 &&
			item.data.map(itemReview => {
				return {
					id: itemReview.id,
					name:
						(itemReview.author.firstName ? itemReview.author.firstName : '') +
						' ' +
						(itemReview.author.lastName ? itemReview.author.lastName[0] + '.' : ''),
					text: itemReview.message,
					stars: itemReview.rating,
				};
			}),
	};
};
