import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { BannerDataApi, BannerDataModel, normolizeBannerData } from '../models/BannerModel';

import { ApiResp, BannerApiData, IBannerApi } from '../BannerApi/types';

export default class BannerNavApi implements IBannerApi {
	private readonly _apiStore = RootStore.apiStore;

	async getBannerData(data: BannerApiData): Promise<ApiResp<BannerDataModel>> {
		const response = await this._apiStore.request<ApiResponse<BannerDataApi, BannerDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/banner/get`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeBannerData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
