import { createAsyncThunk } from '@reduxjs/toolkit';
import CityListApi from 'api/checkout/CityListApi';
import CountryListApi from 'api/checkout/CountryListApi';
import StreetListApi from 'api/checkout/StreetListApi';

const countryListApi = new CountryListApi();
const cityListApi = new CityListApi();
const streetListApi = new StreetListApi();

export const getCountryList = createAsyncThunk('locationData/getCountryList', async (params: { query: string }) => {
	const response = countryListApi.getCuntryList({ query: params.query });
	return response;
});

export const getCityList = createAsyncThunk(
	'locationData/getCityList',
	async (params: { countryId?: string; query: string; limit: number; offset: number }) => {
		const response = cityListApi.getCityList({
			countryId: params.countryId,
			query: params.query,
			limit: params.limit,
			offset: params.offset,
		});
		return response;
	}
);

export const getStreetList = createAsyncThunk('locationData/getStreetList', async (params: { city: string; query: string }) => {
	const response = streetListApi.getStreetList({ city: params.city, query: params.query });
	return response;
});
