import { configureStore } from '@reduxjs/toolkit';
import BreadcrumbsProductStore from 'store/catalog/BreadcrumbsProductStore';
import ProductItemStore from 'store/catalog/ProductItemStore';
import CatalogListStore from 'store/catalog/ProductListStore';
import CheckoutStore from 'store/checkout/CheckoutStore';
import LocationDataStore from 'store/checkout/LocationDataStore';
import PersonalDataStore from 'store/checkout/PersonalDataStore';
import FooterStore from 'store/data/FooterStore';
import HeaderStore from 'store/data/HeaderStore';
import TopBannerStore from 'store/data/TopBannerStore';
import FavoritesStore from 'store/favorites/FavoritesStore';
import IsMobileStore from 'store/isMobile/IsMobileStore';
import LocationStore from 'store/location';
import OrderListStore from 'store/order/OrderListStore';
import CheckoutProductStore from 'store/product/CheckoutProductStore';
import ReviewListStore from 'store/product/ReviewListStore';
import UserDataStore from 'store/user/UserDataStore';

const store = configureStore({
	reducer: {
		catalog: CatalogListStore,
		review: ReviewListStore,
		product: ProductItemStore,
		topBanner: TopBannerStore,
		header: HeaderStore,
		footer: FooterStore,
		breadcrumbsProduct: BreadcrumbsProductStore,
		isMobile: IsMobileStore,
		location: LocationStore,
		userData: UserDataStore,
		checkout: CheckoutStore,
		locationData: LocationDataStore,
		personalData: PersonalDataStore,
		checkoutProduct: CheckoutProductStore,
		favorites: FavoritesStore,
		orderList: OrderListStore,
	},
});

export type RootState = ReturnType<typeof store.getState>;

export default store;
