import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { CatalogFiltersDataApi, CatalogFiltersDataModel, normolizeCatalogData } from '../models/CatalogFiltersModel';

import { ApiResp, CatalogFiltersData, CatalogFiltersParams, ICatalogFiltersApi } from './types';

export default class CatalogFiltersApi implements ICatalogFiltersApi {
	private readonly _apiStore = RootStore.apiStore;

	async getCatalogFiltersData(
		params: CatalogFiltersParams,
		data: CatalogFiltersData
	): Promise<ApiResp<CatalogFiltersDataModel>> {
		const response = await this._apiStore.request<ApiResponse<CatalogFiltersDataApi, CatalogFiltersDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/catalog/filter/${params.id}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeCatalogData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
