import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, CountryListParams, CountryResp, ICountryListApi } from './types';

export default class CountryListApi implements ICountryListApi {
	private readonly _apiStore = RootStore.apiStore;

	async getCuntryList(params: CountryListParams): Promise<ApiResp<CountryResp>> {
		const response = await this._apiStore.request<ApiResponse<CountryResp, CountryResp>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/location/country?query=${params.query}`,
			signal: params.signal,
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}
}
