type OrderListApi = {
	total_count: number;
	total_pages: number;
	data?: {
		id: number;
		number: number;
		status: {
			id: string;
			name: string;
		};
		payed: boolean;
		payUrl: string | null;
		payTemplate: string | null;
		createdAt: string;
		price: number;
		sumPrice: number;
		deliveryPrice: number;
		discount: number;
		sumPaid: number;
		properties: {
			country: string;
			city: string;
			index: string;
			street: string;
			home: string;
			apartment: string;
			phone: string;
		};
		orderProducts: {
			id: number;
			productId: number;
			price: number;
			discount: number;
			quantity: number;
			returned: boolean;
			statusText: string;
			name: string;
			picture: string;
			size: {
				id: number;
				name: string;
			};
			color: {
				id: number;
				name: string;
				sort: number;
				hex: string;
				file: string;
			};
		}[];
		delivery: {
			id: number;
			name: string;
			price: number;
		}[];
	}[];
	message?: string;
};

export type OrderProductItem = {
	id: number;
	date: string;
	price: number;
	priceWithoutDelivery: number;
	deliveryPrice: number;
	payUrl: string | null;
	payTemplate: string | null;
	status: {
		id: string;
		name: string;
	};
	address: string;
	phone: string;
	product: {
		id: number;
		img: string;
		name: string;
		price: number;
		color: string;
		size: string;
		isReturned: boolean;
		statusText: string;
		quantity: number;
	}[];
};

export type OrderListModel = {
	totalCount?: number;
	totalPages?: number;
	orderList?: OrderProductItem[];
	message?: string;
};

export const normalizeOrderListArr = (from: OrderListApi): OrderListModel => {
	if (from.message === 'not found') {
		return {
			message: from.message,
		};
	}
	return {
		totalCount: from.total_count,
		totalPages: from.total_pages,
		orderList: from.data.map(item => {
			return {
				id: item.id,
				date: item.createdAt.split('T')[0].split('-').reverse().join('.'),
				price: item.sumPrice,
				priceWithoutDelivery: item.price,
				deliveryPrice: item.deliveryPrice,
				payUrl: item.payUrl,
				payTemplate: item.payTemplate,
				status: {
					name: item.status.name,
					id: item.status.id,
				},
				address: `${item.properties.city}${item.properties.index ? ', ' + item.properties.index : ''}${
					item.properties.street ? ', ' + item.properties.street : ''
				}${item.properties.home ? ', ' + item.properties.home : ''}${
					item.properties.apartment ? ', ' + item.properties.apartment : ''
				}`,
				phone: item.properties.phone,
				product: item.orderProducts.map(productItem => {
					return {
						id: productItem.productId,
						img: productItem.picture
							? process.env.NEXT_PUBLIC_API_HOST + productItem.picture
							: '/static/img/mock.jpg',
						name: productItem.name,
						price: productItem.price,
						color: productItem.color && productItem.color.name,
						size: productItem.size && productItem.size.name,
						isReturned: productItem.returned,
						statusText: productItem.statusText,
						quantity: productItem.quantity,
					};
				}),
			};
		}),
	};
};
