import { createAsyncThunk } from '@reduxjs/toolkit';
import ReviewApi from 'api/product/ReviewApi';

const reviewListApi = new ReviewApi();

export const getReviewList = createAsyncThunk(
	'review/getreviewList',
	async (params: { id: number; page: number; pageSize: number }) => {
		const response = reviewListApi.getReviewData({ id: params.id, page: params.page, pageSize: params.pageSize });
		return response;
	}
);
