export type LocationDataApi = {
	ip: string;
	hostname: string;
	city: string;
	region: string;
	country: string;
	loc: string;
	org: string;
	postal: string;
	timezone: string;
	readme: string;
};

export type LocationDataModel = {
	country: string;
};

export const normolizeLocationData = (item: LocationDataApi): LocationDataModel => {
	return {
		country: item.country,
	};
};
