import { createAsyncThunk } from '@reduxjs/toolkit';
import OrderListApi from 'api/order/OrderListApi';

const orderListApi = new OrderListApi();

export const getOrderList = createAsyncThunk('orderList/getOrderList', async (params: { pageSize: number }) => {
	const response = await orderListApi.getOrderList({ page: 1, pageSize: params.pageSize });
	return response;
});

export const getOrdersMore = createAsyncThunk('orderList/getOrdersMore', async (params: { page: number; pageSize: number }) => {
	const response = await orderListApi.getOrderList({ page: params.page, pageSize: params.pageSize });
	return response;
});

export const ordersReload = createAsyncThunk('orderList/ordersReload', async (params: { page: number; pageSize: number }) => {
	const response = await orderListApi.getOrderList({ page: params.page, pageSize: params.pageSize });
	return response;
});
