import { createSlice } from '@reduxjs/toolkit';
import { getTopBanner } from './middleware';
import { TopBannerApiData } from 'api/banner/models/TopBannerModel';

type initialStateType = {
	topBanner: TopBannerApiData | null;
	loading: boolean;
	success: boolean;
	error: boolean;
};

const initialState: initialStateType = {
	topBanner: null,
	loading: false,
	success: false,
	error: false,
};

const TopBannerStore = createSlice({
	name: 'topBanner',
	initialState,
	reducers: {},
	extraReducers(builder) {
		builder
			.addCase(getTopBanner.pending, state => {
				state.loading = true;
			})
			.addCase(getTopBanner.fulfilled, (state, action) => {
				state.topBanner = action.payload.data;
				state.success = true;
				state.loading = false;
			})
			.addCase(getTopBanner.rejected, state => {
				state.loading = false;
				state.error = true;
			});
	},
});

export default TopBannerStore.reducer;
