import { createSlice } from '@reduxjs/toolkit';
import { ContactsDataModel } from 'api/footer/models/ContactsModel';
import { getContactsData } from './middleware';

type initialStateType = {
	contacts: ContactsDataModel | null;
	loading: boolean;
	success: boolean;
	error: boolean;
};

const initialState: initialStateType = {
	contacts: null,
	success: false,
	loading: false,
	error: false,
};

const FooterStore = createSlice({
	name: 'footer',
	initialState,
	reducers: {},
	extraReducers(builder) {
		builder
			.addCase(getContactsData.pending, state => {
				state.loading = true;
			})
			.addCase(getContactsData.fulfilled, (state, action) => {
				state.contacts = action.payload.data;
				state.success = true;
				state.loading = false;
			})
			.addCase(getContactsData.rejected, state => {
				state.loading = false;
				state.error = true;
			});
	},
});

export default FooterStore.reducer;
