export type BannerDataApi = {
	data: {
		mobileVideo?: string;
		detailVideo?: string;
		ref: string;
		banner_type_id: string;
		banner_type_name: string;
		annotation: string;
		additional_text: string;
		button_text: string;
		name: string;
		image: string;
		image_mobile: string;
		text: string;
	};
};

export type BannerDataModel = {
	mobileVideo: string;
	detailVideo: string;
	bannerImg: string;
	bannerImgMobile: string;
	annotation: string;
	additionalText: string;
	buttonText: string;
	name: string;
	text: string;
	link: string;
};

export const normolizeBannerData = (item: BannerDataApi): BannerDataModel => {
	if (Array.isArray(item.data) && item.data.length === 0) {
		return null;
	}
	return {
		mobileVideo: item.data.mobileVideo ? process.env.NEXT_PUBLIC_API_HOST + item.data.mobileVideo : '',
		detailVideo: item.data.detailVideo ? process.env.NEXT_PUBLIC_API_HOST + item.data.detailVideo : '',
		bannerImg: item.data.image ? process.env.NEXT_PUBLIC_API_HOST + item.data.image : '',
		bannerImgMobile: item.data.image_mobile ? process.env.NEXT_PUBLIC_API_HOST + item.data.image_mobile : '',
		annotation: item.data.annotation ? item.data.annotation : null,
		additionalText: item.data.additional_text ? item.data.additional_text : null,
		buttonText: item.data.button_text ? item.data.button_text : null,
		name: item.data.name,
		text: item.data.text,
		link: item.data.ref,
	};
};
