import { createSlice } from '@reduxjs/toolkit';
import { getCityList, getCountryList, getStreetList } from './middleware';

type locationTypes = {
	id: string;
	code?: string;
	name: string;
};

type initialStateType = {
	countryies: locationTypes[] | null;
	currentCountry: string;
	currentCountryCode: string;
	cities: locationTypes[] | null;
	currentCity: string;
	currentCityCode: string;
	streets: string[] | null;
	currentStreet: string;
};

const initialState: initialStateType = {
	countryies: null,
	currentCountry: '',
	currentCountryCode: '',
	cities: null,
	currentCity: '',
	currentCityCode: '',
	streets: null,
	currentStreet: '',
};

const LocationDataStore = createSlice({
	name: 'locationData',
	initialState,
	reducers: {
		changeCountryies(state, action) {
			state.countryies = action.payload;
		},
		changeCurrentCountry(state, action) {
			state.currentCountry = action.payload;
		},
		changeCities(state, action) {
			state.cities = action.payload;
		},
		changeCurrentCity(state, action) {
			state.currentCity = action.payload.currentCity;
			state.currentCityCode = action.payload.currentCityCode;
		},
		changeStreets(state, action) {
			state.streets = action.payload;
		},
		changeCurrentStreet(state, action) {
			state.currentStreet = action.payload;
		},
	},
	extraReducers(builder) {
		builder
			.addCase(getCountryList.fulfilled, (state, action) => {
				state.countryies = action.payload.data.data;
				if (action.payload.data.data.length === 0) {
					state.countryies = [{ name: '  ', id: state.currentCountryCode }];
					state.currentCountryCode = '';
				} else {
					state.currentCountryCode = action.payload.data.data[0].code;
				}
			})
			.addCase(getCityList.fulfilled, (state, action) => {
				if (action.meta.arg.offset !== 0) {
					state.cities = state.cities.concat(action.payload.data.data);
				} else {
					if (action.payload.data.data.length === 0) {
						state.cities = [{ name: '  ', id: state.currentCountryCode }];
					} else {
						state.cities = action.payload.data.data;
					}
				}
			})
			.addCase(getStreetList.fulfilled, (state, action) => {
				state.streets = action.payload.data.data;
			});
	},
});

export const { changeCountryies, changeCurrentCountry, changeCities, changeCurrentCity, changeStreets, changeCurrentStreet } =
	LocationDataStore.actions;

export default LocationDataStore.reducer;
