import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';
import Loader from 'components/UI/Loader';

import styles from './Button.module.scss';

type ButtonProps = {
	text?: string;
	isInvisible?: boolean;
	isWhite?: boolean;
	isActive?: boolean;
	className?: string;
	fullWidth?: boolean;
	disabled?: boolean;
	transBorder?: boolean;
	withoutLoader?: boolean;
	buttonRef?: React.Ref<HTMLButtonElement>;
	style?: Record<string, string>;
	children?: React.ReactElement<any, any>;
	onClick?: (event: React.MouseEvent<HTMLButtonElement>) => void;
};

const Button = ({
	text,
	isInvisible,
	isWhite,
	transBorder,
	isActive,
	className,
	disabled,
	withoutLoader,
	fullWidth,
	buttonRef,
	style,
	children,
	onClick,
}: ButtonProps) => {
	return (
		<button
			ref={buttonRef}
			style={style ? style : {}}
			onClick={onClick}
			disabled={disabled}
			className={`${className} ${styles.button} ${isInvisible ? styles.invisible : ''} ${
				fullWidth ? styles.fullWidth : ''
			} ${isWhite ? styles.white : ''} ${isActive ? styles.isActive : ''} ${transBorder ? styles.transBorder : ''}`}
		>
			{children ? (
				children
			) : (
				<>
					{disabled && !withoutLoader ? (
						<Loader white={isActive} imgWidth={21} />
					) : (
						<pre className={`${isInvisible ? SuisseIntl.className : ''} ${isActive ? SuisseIntl.className : ''}`}>
							{text}
						</pre>
					)}
				</>
			)}
		</button>
	);
};

export default React.memo(Button);
