export type DataUserApi = {
	id: number;
	firstName: string;
	lastName: string;
	middleName: string;
	email: string;
	phone: string;
	birthday: string;
	city: string;
	subscribeNewsletter: boolean;
	message?: string;
};

export type DataUserModel = {
	id: number;
	firstName: string;
	lastName: string;
	middleName: string;
	email: string;
	phone: string;
	birthday: string;
	city: string;
	subscribeNewsletter: boolean;
	message?: string;
};

export const normolizeDataUser = (item: DataUserApi): DataUserModel => {
	return {
		id: item.id,
		firstName: item.firstName,
		lastName: item.lastName,
		middleName: item.middleName,
		email: item.email,
		phone: item.phone,
		birthday: item.birthday,
		city: item.city,
		subscribeNewsletter: item.subscribeNewsletter,
		message: item.message ? item.message : null,
	};
};
