import { normalizeOrderArr, orderArrApi, orderArrModel } from 'api/checkout/Models/checkoutModel';
import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, orderMakeData, IOrderApi } from './types';

export default class BasketOrderApi implements IOrderApi {
	private readonly _apiStore = RootStore.apiStore;

	async orderMake(data: orderMakeData): Promise<ApiResp<orderArrModel>> {
		const userUUID = localStorage.getItem('_user_uuid');
		const userIdObj = userUUID && JSON.parse(userUUID);

		const response = await this._apiStore.request<ApiResponse<orderArrApi, orderArrApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/order/?BITRIX_SM_SALE_UID=${userIdObj.BITRIX_SM_SALE_UID}&SALE_USER_ID=${userIdObj.SALE_USER_ID}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data: {
				person_type_id: data.personTypeId,
				delivery_id: data.deliveryId,
				payment_id: data.paymentId,
				location: data.location,
				country: data.country,
				coupon: data.coupon,
				'cancel-coupon': data['cancel-coupon'],
				test: data.test,
				basket: data.basket,
			},
		});
		try {
			response.data = normalizeOrderArr(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
