import { createSlice } from '@reduxjs/toolkit';
import { getCheckoutProduct } from './middleware';

type initialStateType = {
	checkoutProduct: number[];
};

const initialState: initialStateType = {
	checkoutProduct: [],
};

const CheckoutProductStore = createSlice({
	name: 'checkoutProduct',
	initialState,
	reducers: {
		changeCheckoutProduct(state, action) {
			state.checkoutProduct = action.payload;
		},
	},
	extraReducers(builder) {
		builder.addCase(getCheckoutProduct.fulfilled, (state, action) => {
			state.checkoutProduct = action.payload.data ? action.payload.data : [];
		});
	},
});

export const { changeCheckoutProduct } = CheckoutProductStore.actions;

export default CheckoutProductStore.reducer;
