import { createSlice } from '@reduxjs/toolkit';
import { getHeaderData } from './middleware';
import { HeaderDataModel } from 'api/header/models/HeaderModel';

type initialStateType = {
	header: HeaderDataModel | null;
	loading: boolean;
	success: boolean;
	error: boolean;
};

const initialState: initialStateType = {
	header: null,
	loading: false,
	success: false,
	error: false,
};

const HeaderStore = createSlice({
	name: 'footer',
	initialState,
	reducers: {},
	extraReducers(builder) {
		builder
			.addCase(getHeaderData.pending, state => {
				state.loading = true;
			})
			.addCase(getHeaderData.fulfilled, (state, action) => {
				state.header = action.payload.data;
				state.success = true;
				state.loading = false;
			})
			.addCase(getHeaderData.rejected, state => {
				state.loading = false;
				state.error = true;
			});
	},
});

export default HeaderStore.reducer;
