import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { ReviewDataApi, ReviewDataModel, normolizeReviewData } from '../models/ReviewModel';

import { ApiResp, IReviewApi, ReviewDataParams } from './types';

export default class ReviewApi implements IReviewApi {
	private readonly _apiStore = RootStore.apiStore;

	async getReviewData(params: ReviewDataParams): Promise<ApiResp<ReviewDataModel>> {
		const response = await this._apiStore.request<ApiResponse<ReviewDataApi, ReviewDataApi>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/catalog/items/${params.id}/comments?page=${params.page}&pageSize=${params.pageSize}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
		});
		try {
			response.data = normolizeReviewData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
