import { createAsyncThunk } from '@reduxjs/toolkit';
import CheckoutProductListApi from 'api/product/CheckoutProductListApi';

const checkoutProductListApi = new CheckoutProductListApi();

export const getCheckoutProduct = createAsyncThunk(
	'checkoutProduct/getCheckoutProduct',
	async (params: { token: string | null }) => {
		const response = await checkoutProductListApi.getCheckoutProductList({ token: params.token });
		return response;
	}
);
