import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, CityListStoreParams, CityResp, ICityListApi } from './types';

export default class CityListApi implements ICityListApi {
	private readonly _apiStore = RootStore.apiStore;

	async getCityList(params: CityListStoreParams): Promise<ApiResp<CityResp>> {
		const response = await this._apiStore.request<ApiResponse<CityResp, CityResp>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/location/city?${params.countryId ? 'countryId=' + params.countryId + '&' : ''}query=${
				params.query
			}&limit=${params.limit}&offset=${params.offset}`,
			signal: params.signal,
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}
}
