export type SignInDataApi = {
	token: string;
	type: string;
	expire: string;
	errors?: {
		password?: string[];
		email: string[];
		rememberMe?: string[];
		code?: string[];
		phone?: string[];
	};
};

export type SignInModel = {
	token: string;
	errors?: {
		password?: string[];
		email: string[];
		rememberMe?: string[];
		code?: string[];
		phone?: string[];
	};
};

export const normolizeSignIn = (item: SignInDataApi): SignInModel => {
	return {
		token: item.token,
		errors: item.errors
			? {
					password: item.errors.password,
					email: item.errors.email,
					rememberMe: item.errors.rememberMe,
					code: item.errors.code,
					phone: item.errors.phone,
			  }
			: null,
	};
};
