import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { SignInDataApi, SignInModel, normolizeSignIn } from '../models/SignInModel';

import { ApiResp, SignInApiEmailData, ISignInApi, SignInApiPhoneData } from '../SignInApi/types';

export default class SignInApi implements ISignInApi {
	private readonly _apiStore = RootStore.apiStore;

	async getSignInWithEmail(data: SignInApiEmailData): Promise<ApiResp<SignInModel>> {
		const response = await this._apiStore.request<ApiResponse<SignInDataApi, SignInDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/auth/sign-in/email`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeSignIn(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
	async getSignInWithPhone(data: SignInApiPhoneData): Promise<ApiResp<SignInModel>> {
		const response = await this._apiStore.request<ApiResponse<SignInDataApi, SignInDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/auth/sign-in/sms/confirm`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeSignIn(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
