import { createSlice } from '@reduxjs/toolkit';
import { ProductItemDataModel } from 'api/product/models/ProductModel';
import { getProductItem } from './middleware';

type initialStateType = {
	product: ProductItemDataModel | null;
	productIsLoaded: boolean;
};

const initialState: initialStateType = {
	product: null,
	productIsLoaded: true,
};

const ProductItemStore = createSlice({
	name: 'product',
	initialState,
	reducers: {
		changeProduct(state, action) {
			state.product = action.payload.product;
		},
	},
	extraReducers(builder) {
		builder
			.addCase(getProductItem.pending, (state, action) => {
				state.productIsLoaded = false;
			})
			.addCase(getProductItem.fulfilled, (state, action) => {
				state.product = action.payload.data;
				state.productIsLoaded = true;
			});
	},
});

export const { changeProduct } = ProductItemStore.actions;

export default ProductItemStore.reducer;
