import Image from 'next/image';
import React from 'react';

import loader from '/public/static/img/loader.svg';
import loaderWithoutProgress from 'public/static/img/loaderWithoutProgress.svg';
import loaderWithoutProgressWhite from 'public/static/img/loaderWithoutProgressWhite.svg';
import styles from './Loader.module.scss';

type LoaderTypes = {
	imgWidth?: number;
	imgHeight?: number;
	withProgress?: boolean;
	white?: boolean;
};

function getRandomInt(max: number) {
	return Math.floor(Math.random() * max);
}

const Loader = ({ imgWidth, imgHeight, withProgress, white }: LoaderTypes) => {
	const [loadProgress, setLoadProgress] = React.useState<number>(0);

	const progressInterval = React.useRef(null);

	React.useEffect(() => {
		progressInterval.current = setInterval(() => {
			setLoadProgress(prev => {
				if (prev >= 95) return 100;
				return (prev += getRandomInt(5));
			});
		});

		return () => clearInterval(progressInterval.current);
	}, []);

	return (
		<div className={styles.loader}>
			{withProgress ? (
				<Image
					src={loader}
					width={imgWidth || 251}
					height={imgHeight || 72}
					alt=""
					style={imgWidth ? { height: imgHeight || 'auto' } : {}}
				/>
			) : (
				<Image
					src={white ? loaderWithoutProgressWhite : loaderWithoutProgress}
					width={imgWidth || 197}
					height={imgHeight || imgWidth || 197}
					alt=""
					style={imgWidth ? { height: imgHeight || 'auto' } : {}}
				/>
			)}
			{withProgress && <p>{loadProgress} %</p>}
		</div>
	);
};

export default React.memo(Loader);
