import { createAsyncThunk } from '@reduxjs/toolkit';
import UserDataApi from 'api/auth/DataUserApi';
import SignInApi from 'api/auth/SignInApi';

const signInApi = new SignInApi();
const userDataApi = new UserDataApi();

export const getTokenWithEmail = createAsyncThunk(
	'userData/getTokenWithEmail',
	async (params: { email: string; password: string; rememberMe: boolean }) => {
		const response = signInApi.getSignInWithEmail({
			email: params.email,
			password: params.password,
			rememberMe: params.rememberMe,
		});
		return response;
	}
);

export const getTokenWithPhone = createAsyncThunk(
	'userData/getTokenWithPhone',
	async (params: { phone: string; code: string; rememberMe: boolean }) => {
		const response = signInApi.getSignInWithPhone({ phone: params.phone, code: params.code, rememberMe: params.rememberMe });
		return response;
	}
);

export const getUserData = createAsyncThunk('userData/getUserData', async (params: { token: string }) => {
	const response = userDataApi.getDataUser({ token: params.token });
	return response;
});
