import BannerNavApi from 'api/banner/BannerApi';
import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { HeaderDataApi, HeaderDataModel, normolizeHeaderData } from '../models/HeaderModel';

import { ApiResp, IHeaderApi } from './types';

export default class HeaderNavApi implements IHeaderApi {
	private readonly _apiStore = RootStore.apiStore;
	private readonly _bannerNavApi = new BannerNavApi();

	async getHeaderData(): Promise<ApiResp<HeaderDataModel>> {
		const response = await this._apiStore.request<ApiResponse<HeaderDataApi, HeaderDataApi>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/menu/main`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
		});
		const bannerResponseWoman = await this._bannerNavApi.getBannerData({ location: ' ' });
		const bannerResponseMan = await this._bannerNavApi.getBannerData({ location: ' ' });
		const bannerResponseChildren = await this._bannerNavApi.getBannerData({ location: ' ' });
		try {
			response.data = normolizeHeaderData(response.data);
			response.data[0].banner = bannerResponseWoman.data;
			response.data[1].banner = bannerResponseMan.data;
			response.data[2].banner = bannerResponseChildren.data;

			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
