import { createSlice } from '@reduxjs/toolkit';

type initialStateType = {
	home: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	apartment: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	comment: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	email: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	phone: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	surname: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	name: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	lastName: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	street: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	deliveryAddress: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
};

const initialState: initialStateType = {
	home: {
		code: 'HOME',
		isError: false,
		errorText: '',
		value: '',
	},
	apartment: {
		code: 'FLAT',
		isError: false,
		errorText: '',
		value: '',
	},
	comment: {
		code: '',
		isError: false,
		errorText: '',
		value: '',
	},
	email: {
		code: 'EMAIL',
		isError: false,
		errorText: '',
		value: '',
	},
	phone: {
		code: 'PHONE',
		isError: false,
		errorText: '',
		value: '',
	},
	surname: {
		code: 'SECOND_NAME',
		isError: false,
		errorText: '',
		value: '',
	},
	name: {
		code: 'NAME',
		isError: false,
		errorText: '',
		value: '',
	},
	lastName: {
		code: 'SURNAME',
		isError: false,
		errorText: '',
		value: '',
	},
	street: {
		code: 'STREET',
		isError: false,
		errorText: '',
		value: '',
	},
	deliveryAddress: {
		code: 'ADDRESS',
		isError: false,
		errorText: '',
		value: '',
	},
};

const PersonalDataStore = createSlice({
	name: 'personalData',
	initialState,
	reducers: {
		changeAddress(state, action) {
			state.home = { value: action.payload.home, code: state.home.code, isError: false, errorText: '' };
			state.apartment = { value: action.payload.apartment, code: state.apartment.code, isError: false, errorText: '' };
			state.comment = { value: action.payload.comment, code: state.comment.code, isError: false, errorText: '' };
			state.street = { value: state.street.value, code: state.street.code, isError: false, errorText: '' };
		},
		changeStreetError(state) {
			state.street = { value: state.street.value, code: state.street.code, isError: false, errorText: '' };
		},
		changePersonalData(state, action) {
			state.email = { value: action.payload.email, code: state.email.code, isError: false, errorText: '' };
			state.phone = { value: action.payload.phone, code: state.phone.code, isError: false, errorText: '' };
			state.surname = { value: action.payload.surname, code: state.surname.code, isError: false, errorText: '' };
			state.name = { value: action.payload.name, code: state.name.code, isError: false, errorText: '' };
			state.lastName = { value: action.payload.lastName, code: state.lastName.code, isError: false, errorText: '' };
		},
		changeDeliveryAddress(state, action) {
			state.deliveryAddress = { value: action.payload, code: state.deliveryAddress.code, isError: false, errorText: '' };
		},
		changePersonalDataError(state, action) {
			state.home = action.payload.home
				? { ...state.home, isError: !!action.payload.home, errorText: action.payload.home }
				: { ...state.home };
			state.apartment = action.payload.apartment
				? { ...state.apartment, isError: !!action.payload.apartment, errorText: action.payload.apartment }
				: { ...state.apartment };
			state.comment = action.payload.comment
				? { ...state.comment, isError: !!action.payload.comment, errorText: action.payload.comment }
				: { ...state.comment };
			state.email = action.payload.email
				? { ...state.email, isError: !!action.payload.email, errorText: action.payload.email }
				: { ...state.email };
			state.phone = action.payload.phone
				? { ...state.phone, isError: !!action.payload.phone, errorText: action.payload.phone }
				: { ...state.phone };
			state.surname = action.payload.surname
				? { ...state.surname, isError: !!action.payload.surname, errorText: action.payload.surname }
				: { ...state.surname };
			state.name = action.payload.name
				? { ...state.name, isError: !!action.payload.name, errorText: action.payload.name }
				: { ...state.name };
			state.lastName = action.payload.lastName
				? { ...state.lastName, isError: !!action.payload.lastName, errorText: action.payload.lastName }
				: { ...state.lastName };
			state.street = action.payload.street
				? { ...state.street, isError: !!action.payload.street, errorText: action.payload.street }
				: { ...state.street };
			state.deliveryAddress = action.payload.deliveryAddress
				? {
						...state.deliveryAddress,
						isError: !!action.payload.deliveryAddress,
						errorText: action.payload.deliveryAddress,
				  }
				: { ...state.deliveryAddress };
		},
	},
});

export const { changeAddress, changeStreetError, changePersonalData, changeDeliveryAddress, changePersonalDataError } =
	PersonalDataStore.actions;

export default PersonalDataStore.reducer;
