import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, StreetListParams, StreetResp, IStreetListApi } from './types';

export default class StreetListApi implements IStreetListApi {
	private readonly _apiStore = RootStore.apiStore;

	async getStreetList(params: StreetListParams): Promise<ApiResp<StreetResp>> {
		const response = await this._apiStore.request<ApiResponse<StreetResp, StreetResp>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/location/street?city=${params.city}&query=${params.query}`,
			signal: params.signal,
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}
}
