import { ReviewDataApi, ReviewDataModel } from './ReviewModel';

export type ProductItemDataApi = {
	id: number;
	name: string;
	description: string;
	size: {
		id: number;
		name: string;
		sort: number;
	};
	color: {
		id: number;
		name: string;
		sort: number;
	};
	links?: {
		ozon: string;
		wildberries: string;
	};
	price: {
		basePrice: string;
	};
	discount: {
		price: string;
		percent: number;
	} | null;
	podklad: string;
	measurement: {
		name: string;
		sizeData: {
			name: string;
			size: string;
		}[];
	}[];
	material: string;
	pictures: {
		preview: string;
		detail: string;
		more: string[];
	};
	sku: {
		id: number;
		name: string;
		size: {
			id: number;
			name: string;
			sort: number;
		};
		color: {
			id: number;
			name: string;
			sort: number;
			hex: string;
		};
		pictures: {
			preview: string;
			detail: string;
		};
	}[];
};

export type ProductItemDataModel = {
	id?: number;
	images?: string[];
	name?: string;
	price?: number;
	oldPrice?: number;
	discount?: number;
	currentColor?: string;
	colors?: {
		id: number;
		colorId: number;
		name: string;
		hex: string;
		url: string;
	}[];
	links?: {
		ozon: string;
		wildberries: string;
	};
	currentSize?: string;
	size?: {
		name: string;
		sizeId: number;
		id: number;
	}[];
	sizeLabel?: string;
	composition?: string;
	podklad?: string;
	measurement?: {
		name: string;
		sizeData: {
			name: string;
			size: string;
		}[];
	}[];
	delivery?: string[];
	payment?: string;
	message?: string;
	article?: string[];
};

export type ProductDataApi = {
	data?: ProductItemDataApi;
	message?: string;
};

export type ProductDataModel = ProductItemDataModel;

export interface ProductDataModelAll extends ProductDataModel {
	reviews: ReviewDataModel;
}

function removeDuplicates(arr) {
	const colorsList = arr.map(item => item.name);

	let uniqColorsList = Array.from(new Set(colorsList));

	const result = [];

	arr.forEach(item => {
		for (const uniqItem of uniqColorsList) {
			if (uniqItem === item.name) {
				uniqColorsList = uniqColorsList.filter(filterItem => uniqItem !== filterItem);
				result.push(item);
				break;
			}
		}
	});
	return result;
}

export const normolizeProductData = (item: ProductDataApi): ProductDataModel => {
	const itemRes = item.data;
	if (item.message) {
		return {
			message: item.message,
		};
	}

	return {
		id: item.data.id,
		images: [
			itemRes.pictures.detail ? process.env.NEXT_PUBLIC_API_HOST + itemRes.pictures.detail : '/static/img/mock.jpg',
			...(itemRes.pictures.more ? itemRes.pictures.more.map(itemMore => process.env.NEXT_PUBLIC_API_HOST + itemMore) : []),
		],
		name: itemRes.name,
		links: {
			ozon: itemRes?.links.ozon || '',
			wildberries: itemRes?.links.wildberries || '',
		},
		price:
			itemRes.discount && itemRes.discount.price
				? itemRes.discount.price &&
				  parseInt(
						itemRes.discount.price
							.split(' ')
							.slice(0, itemRes.discount.price.split(' ').length - 1)
							.join('')
				  )
				: itemRes.price.basePrice &&
				  parseInt(
						itemRes.price.basePrice
							.split(' ')
							.slice(0, itemRes.price.basePrice.split(' ').length - 1)
							.join('')
				  ),
		oldPrice:
			itemRes.price.basePrice &&
			parseInt(
				itemRes.price.basePrice
					.split(' ')
					.slice(0, itemRes.price.basePrice.split(' ').length - 1)
					.join('')
			),
		discount: itemRes.discount ? itemRes.discount.percent : null,
		currentColor: itemRes.color && itemRes.color.name,
		colors:
			Array.isArray(itemRes.sku) &&
			itemRes.sku.length !== 0 &&
			removeDuplicates([
				...itemRes.sku.map(itemSKU => {
					return {
						id: itemSKU.id,
						colorId: itemSKU.size ? itemSKU.size.id : null,
						name: itemSKU.color ? itemSKU.color.name : null,
						hex: itemSKU.color ? itemSKU.color.hex : null,
						url: itemSKU.pictures.preview
							? process.env.NEXT_PUBLIC_API_HOST + itemSKU.pictures.preview
							: '/static/img/mock.jpg',
					};
				}),
			]),
		currentSize: itemRes.size && itemRes.size.name,
		size: Array.isArray(itemRes.sku) &&
			itemRes.sku.length !== 0 && [
				...itemRes.sku
					.map(itemSKU => {
						return {
							id: itemSKU.id,
							sizeId: itemSKU.color ? itemSKU.color.id : null,
							name: itemSKU.size ? itemSKU.size.name : null,
						};
					})
					.filter(itemFilter => {
						if (itemFilter.sizeId === null && itemFilter.name) return false;
						return itemRes.color && itemRes.color.id === itemFilter.sizeId;
					}),
			],
		sizeLabel: itemRes.description,
		composition: itemRes.material,
		podklad: itemRes.podklad,
		measurement: itemRes.measurement,
		article:
			Array.isArray(itemRes.sku) &&
			itemRes.sku.length !== 0 &&
			itemRes.sku.filter(sku => sku.id === itemRes.id)[0]?.name.split(/\s(.*)/),
		delivery: [
			'           10 000 .,    .',
			'          20 000 ,    .',
			'          .',
			'   1  10         .',
			'  , ,     .       .',
			'      EMS.   3000 .        .',
		],
		payment:
			'        .           .',
	};
};
