import { createSlice } from '@reduxjs/toolkit';
import { ReviewDataModel } from 'api/product/models/ReviewModel';
import { getReviewList } from './middleware';

type initialStateType = {
	reviewList:
		| {
				id: number;
				name: string;
				text: string;
				stars: number;
		  }[]
		| null;
	counter: number;
	totalStars: number;
	totalPages: number;
	currentPage: number;
	reviewIsLoaded: boolean;
};

const initialState: initialStateType = {
	reviewList: null,
	counter: 0,
	totalStars: 0,
	totalPages: 0,
	currentPage: 1,
	reviewIsLoaded: true,
};

const ReviewListStore = createSlice({
	name: 'review',
	initialState,
	reducers: {
		changeReviewList(state, action) {
			state.reviewList = action.payload.reviewList;
		},
		changeCurrentPage(state, action) {
			state.currentPage = action.payload;
		},
		changeTotalPages(state, action) {
			state.totalPages = action.payload;
		},
		changeCounter(state, action) {
			state.counter = action.payload;
		},
		changeTotalStars(state, action) {
			state.totalStars = action.payload.totalStars;
		},
	},
	extraReducers(builder) {
		builder
			.addCase(getReviewList.pending, (state, action) => {
				state.reviewIsLoaded = false;
			})
			.addCase(getReviewList.fulfilled, (state, action) => {
				state.reviewList = action.payload.data.reviewsList;
				state.totalPages = action.payload.data.totalPages;
				state.reviewIsLoaded = true;
			});
	},
});

export const { changeReviewList, changeCurrentPage, changeTotalPages, changeCounter, changeTotalStars } = ReviewListStore.actions;

export default ReviewListStore.reducer;
