export type BreadcrumbsDataApi = {
	id: number;
	code: string;
	name: string;
	level: string;
}[];

export type BreadcrumbsDataModel = {
	id: number;
	code: string;
	name: string;
}[];

export const normolizeBreadcrumbsData = (item: BreadcrumbsDataApi): BreadcrumbsDataModel => {
	return item.map(itemRes => {
		return {
			id: itemRes.id,
			code: itemRes.code,
			name: itemRes.name,
		};
	});
};
