/* eslint-disable max-len */
import 'styles/normolize.scss';
import 'styles/root.scss';
//import 'styles/fonts.scss';
import { appWithTranslation } from 'next-i18next';
import type { AppProps } from 'next/app';
import { Provider } from 'react-redux';
import store from 'store/RootStore';
import { useRouter } from 'next/router';
import Head from 'next/head';
import React from 'react';
import dynamic from 'next/dynamic';
import { IBMPlexMono } from 'helpers/utils/fonts';
import LocationApi from 'api/location/LocationApi';
import ErrorBoundary from 'components/ErrorBoundary';
import WithYandexMetrika from 'components/WithYandexMetrika';
import { changeLocation } from 'store/location';
import routes from 'helpers/routes';
import ConsistentDataProvider from 'components/ConsistentDataProvider';
import { useUserData } from 'helpers/hooks/useUserData';

function MyApp({ Component, pageProps }: AppProps) {
	useUserData();

	const router = useRouter();
	const PersonalArea = dynamic(() => import('components/pages/PersonalArea'));
	const ChatButton = dynamic(() => import('components/ChatButton'));

	React.useEffect(() => {
		if (!!router.query['personal-area']) {
			document.querySelector('body').style.overflowY = 'hidden';
			document.querySelector('body').style.touchAction = 'none';
		} else {
			document.querySelector('body').style.overflow = 'hidden scroll';
			document.querySelector('body').style.touchAction = '';
		}
	}, [router]);

	React.useEffect(() => {
		if (!localStorage.getItem('location')) {
			const locationApi = new LocationApi();
			locationApi.getLocationData().then(res => {
				localStorage.setItem('location', res.data.country);
				store.dispatch(changeLocation(res.data.country));
			});
		} else {
			store.dispatch(changeLocation(localStorage.getItem('location')));
		}
		//   
		process.env.NEXT_PUBLIC_IS_PRODUCTION === 'false' && (window['disableYaCounter85251295'] = true);
		(window as any).dataLayer = (window as any).dataLayer || [];
	}, []);

	React.useEffect(() => {
		router.events.on('routeChangeStart', (url, { shallow }) => {
			if (!url.includes(routes.catalog) && !shallow) {
				const scrollPosition = window.pageYOffset.toString();
				sessionStorage.setItem('scrollPosition_' + document.location.pathname + document.location.search, scrollPosition);
			}
		});
	}, [router]);

	return (
		<main className={IBMPlexMono.className}>
			<Provider store={store}>
				<ConsistentDataProvider>
					<WithYandexMetrika>
						<Head>
							<title>     TOTTI</title>
							<meta
								name="description"
								content="TOTTI I  ,  .     ,   . ,   ."
							></meta>
						</Head>
						<PersonalArea
							isOpen={!!router.query['personal-area']}
							action={router.query['personal-area']}
							onRequestClose={() =>
								router.push(
									{
										pathname: router.asPath.split('?')[0],
										query: {
											...router.query,
											name: [],
											'personal-area': [],
											section: [],
										},
									},
									undefined,
									{ shallow: true }
								)
							}
						/>
						<ErrorBoundary>
							<Component {...pageProps} />
							{router.pathname !== '/product-card/[name]' && <ChatButton isNotOpacity={router.pathname !== '/'} />}
						</ErrorBoundary>
					</WithYandexMetrika>
				</ConsistentDataProvider>
			</Provider>
		</main>
	);
}

export default appWithTranslation(MyApp);
