import { createAsyncThunk } from '@reduxjs/toolkit';
import CatalogListApi from 'api/catalog/CatalogApi';
import CatalogFiltersApi from 'api/catalog/CatalogFiltersApi';

const catalogListApi = new CatalogListApi();
const catalogFiltersApi = new CatalogFiltersApi();

export const getCatalogList = createAsyncThunk(
	'catalog/getCatalogList',
	async (params: {
		search: string;
		sortParam: string;
		sortType: string;
		page: number;
		pageSize: number;
		filters: {
			section: number[];
			size?: number[];
			color?: number[];
		};
	}) => {
		const response = catalogListApi.getCatalogListData(
			{
				search: params.search,
				sortParam: params.sortParam,
				sortType: params.sortType,
				page: params.page,
				pageSize: params.pageSize,
			},
			{ filters: params.filters }
		);
		return response;
	}
);

export const getFilterList = createAsyncThunk(
	'catalog/getFilterList',
	async (params: {
		id: number;
		filters: {
			section: number[];
			size?: number[];
			color?: number[];
		};
	}) => {
		const response = catalogFiltersApi.getCatalogFiltersData({ id: params.id }, { filters: params.filters });
		return response;
	}
);
