type CatalogItemDataApi = {
	id: number;
	img: string[];
	title: string;
	price: number;
	oldPrice: number;
	discount: number;
	date: string;
	sku: sku[];
};

export interface sku {
	id: number;
	name: string;
	size: Size;
	color: Color;
}
export interface Size {
	id: number;
	name: string;
	sort: number;
}
export interface Color {
	id: number;
	name: string;
	sort: number;
	hex: string;
}

export type CatalogItemDataModel = {
	id: number;
	images: string[];
	secondImg: string;
	title: string;
	price: number;
	oldPrice: number;
	isSale: boolean;
	persentSale: number;
	size: string[];
	colors: string[];
	colorNames: string[];
	activeColor: string;
};

export type CatalogListDataApi = {
	total_counter: number;
	total_pages: number;
	catalog_name: string;
	data: CatalogItemDataApi[];
};

export type CatalogListDataModel = {
	totalCounter: number;
	totalPages: number;
	catalogName: string;
	data: CatalogItemDataModel[];
};

function removeDuplicates(arr: sku[]) {
	const colorsList = arr.map(item => item.color.name);

	let uniqColorsList = Array.from(new Set(colorsList));

	const result = [];

	arr.forEach(item => {
		for (const uniqItem of uniqColorsList) {
			if (uniqItem === item.color.name) {
				uniqColorsList = uniqColorsList.filter(filterItem => uniqItem !== filterItem);
				result.push(item);
				break;
			}
		}
	});
	return result;
}

export const normolizeCatalogData = (item: CatalogListDataApi): CatalogListDataModel => {
	return {
		totalCounter: item.total_counter,
		totalPages: item.total_pages,
		catalogName: item.catalog_name,
		data: item.data
			.filter(filterItem => {
				if (
					filterItem.img &&
					Array.isArray(filterItem.img) &&
					filterItem.img.every(everyItem => everyItem !== null) &&
					filterItem.img.length !== 0
				)
					return true;
				return false;
			})
			.map(mapItem => {
				mapItem.sku = mapItem.sku.map(skuItem => {
					if (!skuItem.color) {
						return {
							...skuItem,
							color: {
								id: 0,
								name: ' ',
								sort: 0,
								hex: '#0000',
							},
						};
					} else {
						return skuItem;
					}
				});
				return {
					id: mapItem.id,
					images: mapItem.img.map(itemMap => process.env.NEXT_PUBLIC_API_HOST + itemMap),
					secondImg: Array.isArray(mapItem.img)
						? mapItem.img[1]
							? process.env.NEXT_PUBLIC_API_HOST + mapItem.img[1]
							: ''
						: '',
					title: mapItem.title,
					price: mapItem.price,
					oldPrice: mapItem.oldPrice,
					isSale: !!mapItem.discount,
					persentSale: mapItem.discount || null,
					size:
						mapItem.sku &&
						Array.isArray(mapItem.sku) &&
						mapItem.sku.length !== 0 &&
						mapItem.sku.map(sku => sku.size.id + ':' + sku.size.name + ':' + (sku.id === mapItem.id)),
					colors: removeDuplicates(mapItem.sku).map(sku => sku.color.hex),
					colorNames: removeDuplicates(mapItem.sku).map(sku => sku.color.name),
					activeColor: mapItem.sku.find(sku => sku.id === mapItem.id).color.hex,
				};
			}),
	};
};
