import RootStore from 'api/RootApi';
import { ApiResp, ITopBannerApi } from './types';
import { TopBannerApiData } from '../models/TopBannerModel';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

export default class TopBannerApi implements ITopBannerApi {
	private readonly _apiStore = RootStore.apiStore;

	async getTopBanner(): Promise<ApiResp<TopBannerApiData>> {
		const response = await this._apiStore.request<ApiResponse<TopBannerApiData, TopBannerApiData>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/banner/top-banner`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
		});
		return {
			success: response.success,
			data: response.data.message ? null : response.data,
			status: response.status,
		};
	}
}
