export type favoriteItemApi = {
	id: number;
	name: string;
	price: number;
	color: {
		id: number;
		name: string;
		hex: string;
		file: string;
		sort: number;
	};
	size: {
		id: number;
		name: string;
		sort: number;
	};
	picture: string;
	sku: {
		id: number;
		color: {
			id: number;
			name: string;
			hex: string;
			file: string;
			sort: number;
		};
		size: {
			id: number;
			name: string;
			sort: number;
		};
	}[];
};

export type favoriteItemModel = {
	id: number;
	img: string;
	name: string;
	price: number;
	color: string;
	sizeList: {
		id: number;
		name: string;
	}[];
};

export type FavoritesApi = {
	data: favoriteItemApi[];
};

export type FavoritesModel = favoriteItemModel[];

export const normalizeFavoritesArr = (from: FavoritesApi): FavoritesModel => {
	return from.data.map(item => {
		return {
			id: item.id,
			img: item.picture ? process.env.NEXT_PUBLIC_API_HOST + item.picture : '/static/img/mock.jpg',
			name: item.name,
			price: item.price,
			color: item.color ? item.color.name : '',
			sizeList: item.size
				? [
						{
							id: item.id,
							name: item.size.name,
							...Object.values(item.sku)
								.filter(filterItem => {
									if (!item.color) return true;
									return filterItem.color.name === item.color.name;
								})
								.map(mapItem => {
									return {
										id: mapItem.id,
										name: mapItem.size.name,
									};
								}),
						},
				  ]
				: Object.values(item.sku)
						.filter(filterItem => {
							if (!item.color) return false;
							if (!filterItem.size) return false;
							return filterItem.color.name === item.color.name;
						})
						.map(mapItem => {
							return {
								id: mapItem.id,
								name: mapItem.size.name,
							};
						}),
		};
	});
};
