import { createAsyncThunk } from '@reduxjs/toolkit';
import TopBannerApi from 'api/banner/TopBannerApi';

const topBanner = new TopBannerApi();

export const getTopBanner = createAsyncThunk('topBanner/getTopBanner', async () => {
	const response = await topBanner.getTopBanner();
	return response;
});
