import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, CheckoutProductListParams, ICheckoutProductListApi, responseData } from './types';

export default class CheckoutProductListApi implements ICheckoutProductListApi {
	private readonly _apiStore = RootStore.apiStore;

	async getCheckoutProductList(params: CheckoutProductListParams): Promise<ApiResp> {
		const userUUID = localStorage.getItem('_user_uuid');
		const userIdObj = userUUID && JSON.parse(userUUID);
		const response = await this._apiStore.request<ApiResponse<responseData, responseData>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/basket?BITRIX_SM_SALE_UID=${userIdObj.BITRIX_SM_SALE_UID}&SALE_USER_ID=${userIdObj.SALE_USER_ID}`,
			headers: params.token
				? {
						accept: 'application/json',
						'Content-Type': 'application/json',
						Authorization: `Bearer ${params.token}`,
				  }
				: {
						accept: 'application/json',
						'Content-Type': 'application/json',
				  },
		});
		try {
			return {
				success: response.success,
				data: response.data.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
