type CatalogFiltersColorItemApi = {
	id: number;
	name: string;
};

export type CatalogFiltersColorItemModel = {
	id: number;
	name: string;
};

type CatalogFiltersSizesItemApi = {
	id: number;
	name: string;
};

export type CatalogFiltersSizesItemModel = {
	id: number;
	name: string;
};

type CatalogFiltersSectionsItemApi = {
	id: number;
	name: string;
};

export type CatalogFiltersSectionsItemModel = {
	id: number;
	name: string;
};

export type CatalogFiltersDataApi = {
	colors: CatalogFiltersColorItemApi[];
	sizes: CatalogFiltersSizesItemApi[];
	section: CatalogFiltersSectionsItemApi[];
};

export type CatalogFiltersDataModel = {
	colors: CatalogFiltersColorItemModel[];
	dimensions: CatalogFiltersSizesItemModel[];
	categories: CatalogFiltersSectionsItemModel[];
};

export const normolizeCatalogData = (item: CatalogFiltersDataApi): CatalogFiltersDataModel => {
	return {
		colors:
			item.colors && Array.isArray(item.colors) && item.colors.length !== 0 && [{ id: 0, name: '' }].concat(item.colors),
		dimensions:
			item.sizes && Array.isArray(item.sizes) && item.sizes.length !== 0 && [{ id: 0, name: '' }].concat(item.sizes),
		categories:
			item.section && Array.isArray(item.section) && item.section.length === 0
				? []
				: !Array.isArray(item.section)
				? [{ id: 0, name: '' }]
				: [{ id: 0, name: '' }].concat(item.section),
	};
};
