import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { FavoritesModel, normalizeFavoritesArr } from '../models/favorites';

import {
	ApiFavoritesIdsResp,
	ApiFavoritesListResp,
	ApiResp,
	FavoritesData,
	FavoritesTokenParams,
	FavoritesIdsResp,
	FavoritesResp,
	IFavoritesApi,
} from './types';

export default class FavoritesApi implements IFavoritesApi {
	private readonly _apiStore = RootStore.apiStore;

	async addToFavorites(data: FavoritesData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<FavoritesResp, FavoritesResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/favorites`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data: data,
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}

	async getFavoritesIds(params: FavoritesTokenParams): Promise<ApiFavoritesIdsResp> {
		const response = await this._apiStore.request<ApiResponse<FavoritesIdsResp, FavoritesIdsResp>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/favorites/ids`,
			headers: params.token
				? {
						accept: 'application/json',
						'Content-Type': 'application/json',
						Authorization: `Bearer ${params.token}`,
				  }
				: {
						accept: 'application/json',
						'Content-Type': 'application/json',
				  },
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}

	async deleteFavorites(data: FavoritesData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<FavoritesResp, FavoritesResp>>({
			method: HTTPMethod.DELETE,
			endpoint: `/api/v1/favorites/${data.id}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}

	async getFavoritesList(params: FavoritesTokenParams): Promise<ApiFavoritesListResp> {
		const response = await this._apiStore.request<ApiResponse<FavoritesModel, FavoritesModel>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/favorites`,
			headers: params.token
				? {
						accept: 'application/json',
						'Content-Type': 'application/json',
						Authorization: `Bearer ${params.token}`,
				  }
				: {
						accept: 'application/json',
						'Content-Type': 'application/json',
				  },
		});
		response.data = normalizeFavoritesArr(response.data);
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}
}
