import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { ContactsDataApi, ContactsDataModel, normolizeContactsData } from '../models/ContactsModel';

import { ApiResp, IContactsApi } from './types';

export default class ContactsApi implements IContactsApi {
	private readonly _apiStore = RootStore.apiStore;

	async getContactsData(): Promise<ApiResp<ContactsDataModel>> {
		const response = await this._apiStore.request<ApiResponse<ContactsDataApi, ContactsDataApi>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/params`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
		});
		try {
			response.data = normolizeContactsData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
