import { formattedPrice } from 'helpers/utils';

export type orderArrApi = {
	BASKET_INFO: {
		BASKET_ITEMS: {
			ID: string;
			NAME: string;
			PRICE: number;
			FORMATTED_PRICE: string;
			FULL_PRICE: number;
			FORMATTED_FULL_PRICE: string;
			DISCOUNT_PRICE: number;
			FORMATTED_DISCOUNT_PRICE: string;
			QUANTITY: number;
			QUANTITY_STORE: number;
			DETAIL_PAGE_URL: string;
			NAV: {
				ID: string;
				NAME: string;
			}[];
			COLOR: {
				CODE: string;
				ID: number;
				VALUE: string;
				SORT: number;
				NAME: string;
				PICTURE: string;
			};
			CLOTHES_SIZE: {
				CODE: string;
				ID: number;
				VALUE: string;
				SORT: number;
				NAME: string;
			};
			DETAIL_PICTURE: string;
			SKU: {
				[identifier: string]: {
					ID: number;
					NAME: string;
					PARENT_ID: number;
					COLOR: {
						UF_NAME: string;
						UF_XML_ID: string;
						UF_FILE: string;
						ID: number;
					};
					SIZE: {
						NAME: string;
						ENUM_ID: number;
						VALUE_ID: number;
					};
				};
			};
		}[];
		COUPON: {
			COUPON: string;
			MODE: number;
			STATUS: number;
			CHECK_CODE: number;
			MODULE: string;
			MODULE_ID: string;
			ID: number;
			DISCOUNT_ID: number;
			DISCOUNT_NAME: string;
			TYPE: number;
			ACTIVE: string;
			USER_INFO: any[];
			SAVED: string;
			DISCOUNT_ACTIVE: string;
			DISCOUNT_ACTIVE_FROM: string;
			DISCOUNT_ACTIVE_TO: string;
			SORT: number;
			DELIVERY: any[];
			STATUS_TEXT: string;
			CHECK_CODE_TEXT: string[];
			STATUS_CODE: string;
		};
		TOTAL: {
			ORDER_PRICE: number;
			ORDER_PRICE_FORMATED: string;
			ORDER_WEIGHT: number;
			ORDER_WEIGHT_FORMATED: number;
			PRICE_WITHOUT_DISCOUNT_VALUE: number;
			PRICE_WITHOUT_DISCOUNT: string;
			DISCOUNT_PRICE: number;
			FORMATTED_DISCOUNT_PRICE: string;
			DELIVERY_PRICE: number;
			FORMATTED_DELIVERY_PRICE: string;
			FULL_PRICE: number;
			FORMATTED_FULL_PRICE: string;
		};
	};
	DATA: {
		DELIVERY: {
			ID: string;
			NAME: string;
			DELIVERY: {
				ID: string;
				NAME: string;
				DESCRIPTION: string;
				PARENT_ID: number;
				CHECKED: string;
				CONFIG: {
					MAIN: {
						CURRENCY: string;
						PERIOD: {
							FROM: number;
							TO: number;
							TYPE: string;
						};
						PRICE: number;
					};
				};
				XML_ID: string;
			}[];
		}[];
		FIELDS: {
			ID: string;
			NAME: string;
			TYPE: string;
			REQUIRED: string;
			DEFAULT_VALUE: string;
			CODE: string;
			VALUE: string;
		}[];
		PAYMENTS: {
			ID: string;
			NAME: string;
			CODE: string;
			DESCRIPTION: string;
			ACTION_FILE: string;
			CHECKED: string;
		}[];
	};
	SERVICES: {
		DELIVERY_IDS: {
			DEL_MOSC: number;
		};
	};
	STATUS: string;
	MESSAGE?: string;
};

export type orderArrModel = {
	products?: {
		id: number;
		img: string;
		name: string;
		color: string;
		price: string;
		category: string;
		sizeList: {
			id: number;
			name: string;
		}[];
		currentSize: string;
		count: number;
		storeCount: number;
	}[];
	promo?: string;
	checkoutDeliveryRadio?: {
		id: number;
		code: string;
		value: boolean;
		label: string;
		disable?: boolean;
		description: string;
		price: number;
	}[];
	checkoutPickupRadio?: {
		id: number;
		code: string;
		value: boolean;
		label: string;
		disable?: boolean;
		description: string;
		price: number;
	}[];
	checkoutPaymentRadio?: {
		id: number;
		value: boolean;
		label: string;
		code: string;
		disable?: boolean;
		//isPrompt: boolean;
	}[];
	fields?: {
		code: string;
		id: string;
		value: string;
	}[];
	promoIsSuccess?: boolean;
	productCount?: number;
	price?: number;
	discount?: number;
	//personalDiscountWithPercent: number,
	//personalDiscount: number,
	deliveryPrice?: number;
	totalPrice?: number;
	status?: string;
	message?: string;
};

export const normalizeOrderArr = (from: orderArrApi): orderArrModel => {
	if (from.MESSAGE === 'Properties not found') {
		return {
			status: 'fail',
			message: 'Properties not found',
		};
	}

	const productCount = from.BASKET_INFO.BASKET_ITEMS.reduce((counter, item) => {
		return (counter += item.QUANTITY);
	}, 0);

	const pickup = from.DATA.DELIVERY.find(item => item.NAME === '')?.DELIVERY.find(item => item.XML_ID === 'pickup');

	return {
		products: from.BASKET_INFO.BASKET_ITEMS.map(item => {
			return {
				id: parseInt(item.ID),
				img: item.DETAIL_PICTURE ? process.env.NEXT_PUBLIC_API_HOST + item.DETAIL_PICTURE : '/static/img/mock.jpg',
				name: item.NAME,
				color: item.COLOR ? item.COLOR.NAME : null,
				price: formattedPrice(item.PRICE),
				category: item.NAV.map(cat => cat.NAME).join('/'),
				sizeList:
					item.SKU !== null &&
					Object.values(item.SKU)
						.filter(itemFilter =>
							itemFilter.COLOR && item.COLOR ? itemFilter.COLOR.UF_NAME === item.COLOR.NAME : false
						)
						.map(itemSKU => {
							return {
								id: itemSKU.ID,
								name: itemSKU.SIZE.NAME,
							};
						}),
				currentSize: item.CLOTHES_SIZE ? item.CLOTHES_SIZE.VALUE : null,
				count: item.QUANTITY,
				storeCount: item.QUANTITY_STORE,
			};
		}),
		promo: from.BASKET_INFO.COUPON ? from.BASKET_INFO.COUPON.COUPON : '',
		checkoutDeliveryRadio: !from.DATA.DELIVERY.find(item => item.NAME === '')
			? []
			: from.DATA.DELIVERY.find(item => item.NAME === '').DELIVERY.map(item => {
					return {
						id: parseInt(item.ID),
						code: item.XML_ID,
						value: item.CHECKED === 'Y',
						label: item.NAME,
						description: item.DESCRIPTION,
						price: item.CONFIG.MAIN.PRICE,
					};
			  }),
		checkoutPickupRadio: !from.DATA.DELIVERY.find(item => item.NAME === '')
			? []
			: from.DATA.DELIVERY.find(item => item.NAME === '').DELIVERY.map(item => {
					return {
						id: parseInt(item.ID),
						code: item.XML_ID,
						value: item.CHECKED === 'Y',
						label: item.NAME,
						description: item.DESCRIPTION,
						price: item.CONFIG.MAIN.PRICE,
					};
			  }),
		checkoutPaymentRadio: from.DATA.PAYMENTS
			? from.DATA.PAYMENTS.map(item => {
					return {
						id: parseInt(item.ID),
						value: item.CHECKED === 'Y',
						label: item.NAME,
						disable: productCount >= 5 && pickup?.CHECKED === 'Y' && item.CODE === 'PAYMENT_CASH' ? true : false,
						code: item.CODE,
						//isPrompt: item.CODE === 'DOLYAMI' || item.CODE === 'TINKOFF_RASSROCHKA',
					};
			  })
			: null,
		fields: from.DATA.FIELDS.map(item => {
			return {
				code: item.CODE,
				id: item.ID,
				value: item.VALUE || item.DEFAULT_VALUE,
			};
		}),
		promoIsSuccess: from.BASKET_INFO.COUPON.STATUS_CODE === 'APPLIED',
		productCount: productCount,
		price: from.BASKET_INFO.TOTAL.PRICE_WITHOUT_DISCOUNT_VALUE,
		discount: from.BASKET_INFO.TOTAL.DISCOUNT_PRICE,
		deliveryPrice: from.BASKET_INFO.TOTAL.DELIVERY_PRICE,
		totalPrice: from.BASKET_INFO.TOTAL.FULL_PRICE,
	};
};
