import { createSlice } from '@reduxjs/toolkit';
import { orderArrModel } from 'api/checkout/Models/checkoutModel';
import { getCheckoutData } from './middleware';

type initialStateType = {
	checkout: orderArrModel | null;
	deliveryId: number | undefined;
	paymentId: number | undefined;
	sdekError: boolean;
	promo: string;
	checkoutIsLoaded: boolean;
};

const initialState: initialStateType = {
	checkout: null,
	deliveryId: undefined,
	paymentId: undefined,
	sdekError: false,
	promo: '',
	checkoutIsLoaded: true,
};

const CheckoutStore = createSlice({
	name: 'checkout',
	initialState,
	reducers: {
		changeCheckout(state, action) {
			state.checkout = action.payload.checkout;
		},
		setSdekError(state) {
			state.sdekError = true;
		},
	},
	extraReducers(builder) {
		builder
			.addCase(getCheckoutData.pending, (state, action) => {
				state.checkoutIsLoaded = false;
			})
			.addCase(getCheckoutData.fulfilled, (state, action) => {
				state.checkout = action.payload.data;
				state.checkoutIsLoaded = true;
				if (action.payload.data.message === 'Properties not found') {
					return;
				}
				const currentDelivery = action.payload.data.checkoutDeliveryRadio.find(item => item.value === true);
				const currentPickup = action.payload.data.checkoutPickupRadio.find(item => item.value === true);
				state.deliveryId = currentDelivery ? currentDelivery.id : currentPickup?.id;
				const currentPayment = action.payload.data.checkoutPaymentRadio.find(item => item.value === true);
				state.paymentId = currentPayment?.id;
				state.promo = action.payload.data.promo;
			});
	},
});

export const { changeCheckout, setSdekError } = CheckoutStore.actions;

export default CheckoutStore.reducer;
