import { createAsyncThunk } from '@reduxjs/toolkit';
import BasketOrderApi from 'api/checkout/BasketOrderApi';
import ProductApi from 'api/product/ProductApi';

const basketOrderApi = new BasketOrderApi();

export const getCheckoutData = createAsyncThunk(
	'checkout/getCheckoutData',
	async (params: {
		deliveryId?: number;
		paymentId?: number;
		personTypeId?: number;
		location?: string;
		country?: string;
		coupon?: string;
		'cancel-coupon'?: string;
		basket?: any;
		signal?: AbortSignal;
	}) => {
		const response = basketOrderApi.orderMake({
			deliveryId: params.deliveryId,
			paymentId: params.paymentId,
			personTypeId: params.personTypeId,
			location: params.location,
			country: params.country,
			coupon: params.coupon,
			'cancel-coupon': params['cancel-coupon'],
			basket: params.basket,
			signal: params.signal,
		});
		return response;
	}
);
