import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { CatalogListDataApi, CatalogListDataModel, normolizeCatalogData } from '../models/CatalogListModel';

import { ApiResp, CatalogListData, CatalogListParams, ICatalogListApi } from './types';

export default class CatalogListApi implements ICatalogListApi {
	private readonly _apiStore = RootStore.apiStore;

	async getCatalogListData(params: CatalogListParams, data: CatalogListData): Promise<ApiResp<CatalogListDataModel>> {
		const response = await this._apiStore.request<ApiResponse<CatalogListDataApi, CatalogListDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/catalog/list?search=${params.search}&sortParam=${params.sortParam}&sortType=${params.sortType}&page=${params.page}&pageSize=${params.pageSize}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeCatalogData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
