import axios from 'axios';
import useSWRImmutable from 'swr/immutable';

async function fetcher<T>(url: string): Promise<T> {
	const userUUID = localStorage.getItem('_user_uuid');
	if (!!userUUID) {
		return await JSON.parse(userUUID);
	}

	const res = await axios.get(url);
	localStorage.setItem('_user_uuid', JSON.stringify(res.data.data));
	return res.data.data;
}

export const useUserData = () => {
	useSWRImmutable<any>(process.env.NEXT_PUBLIC_API_HOST + '/api/v1/uid', fetcher);
};
