import { createAsyncThunk } from '@reduxjs/toolkit';
import FavoritesApi from 'api/favorites/FavoritesApi';
import CheckoutProductListApi from 'api/product/CheckoutProductListApi';

const favoritesApi = new FavoritesApi();

export const getFavoritesIds = createAsyncThunk('favorites/getFavoritesIds', async (params: { token: string | null }) => {
	const response = await favoritesApi.getFavoritesIds({ token: params.token });
	return response;
});

export const getFavoritesList = createAsyncThunk('favorites/getFavoritesList', async (params: { token: string | null }) => {
	const response = await favoritesApi.getFavoritesList({ token: params.token });
	return response;
});
