import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { DataUserApi, DataUserModel, normolizeDataUser } from '../models/DataUserModel';

import { ApiResp, DataUserApiData, IDataUserApi } from '../DataUserApi/types';

export default class UserDataApi implements IDataUserApi {
	private readonly _apiStore = RootStore.apiStore;

	async getDataUser(data: DataUserApiData): Promise<ApiResp<DataUserModel>> {
		const response = await this._apiStore.request<ApiResponse<DataUserApi, DataUserApi>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/me`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
				Authorization: `Bearer ${data.token}`,
			},
			data,
		});
		try {
			response.data = normolizeDataUser(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
