import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { useAppSelector } from 'helpers/hooks/useAppSelector';
import React from 'react';
import { getContactsData } from 'store/data/FooterStore/middleware';
import { getHeaderData } from 'store/data/HeaderStore/middleware';

const ConsistentDataProvider = ({ children }: React.PropsWithChildren) => {
	const dispatch = useAppDispatch();
	const successFooter = useAppSelector(state => state.footer.success);
	const successHeader = useAppSelector(state => state.header.success);
	React.useEffect(() => {
		if (!successFooter && !successHeader) {
			Promise.all([dispatch(getHeaderData()), dispatch(getContactsData())]);
		}
	}, [dispatch, successFooter, successHeader]);

	return <>{children}</>;
};

export default ConsistentDataProvider;
