import { createSlice } from '@reduxjs/toolkit';
import { BreadcrumbsDataModel } from 'api/breadcrumbs/models/BreadcrumbsModel';
import { getBreadcrumbsProduct } from './middleware';

type initialStateType = {
	links: BreadcrumbsDataModel | null;
};

const initialState: initialStateType = {
	links: null,
};

const BreadcrumbsProductStore = createSlice({
	name: 'breadcrumbsProduct',
	initialState,
	reducers: {
		changeBreadcrumbsProduct(state, action) {
			state.links = action.payload;
		},
	},
	extraReducers(builder) {
		builder.addCase(getBreadcrumbsProduct.fulfilled, (state, action) => {
			state.links = action.payload.data;
		});
	},
});

export const { changeBreadcrumbsProduct } = BreadcrumbsProductStore.actions;

export default BreadcrumbsProductStore.reducer;
