import { createSlice } from '@reduxjs/toolkit';

type initialStateType = {
	isMobile: boolean;
	test?: boolean;
};

const initialState: initialStateType = {
	isMobile: false,
	test: false,
};

const IsMobileStore = createSlice({
	name: 'isMobile',
	initialState,
	reducers: {
		changeIsMobile(state, action) {
			state.isMobile = action.payload;
		},
	},
});

export const { changeIsMobile } = IsMobileStore.actions;

export default IsMobileStore.reducer;
