import RootStore from 'store/RootStore';
import { ApiResponse, HTTPMethod, IApiStore, RequestParams, StatusHTTP } from './types';

export default class ApiStore implements IApiStore {
	readonly baseUrl: string;
	constructor(baseUrl: string) {
		this.baseUrl = baseUrl;
	}
	// eslint-disable-next-line
	async request<SuccessT, ErrorT = any, ReqT = {}>(params: RequestParams<ReqT>): Promise<ApiResponse<SuccessT, ErrorT>> {
		try {
			const url = `${this.baseUrl}${params.endpoint}`;
			let data: string | null = null;
			if (params.method === HTTPMethod.POST || params.method === HTTPMethod.PUT) {
				data = JSON.stringify(params.data);
			}
			const { token } = RootStore.getState().userData;
			const { location } = RootStore.getState().location;
			const headers = {
				...params.headers,
				'Access-Control-Request-Headers': 'location',
			};
			const response = await fetch(url, {
				method: params.method,
				headers:
					token && location
						? { ...headers, Authorization: `Bearer ${token}`, location }
						: location
						? { ...headers, location }
						: token
						? { ...headers, Authorization: `Bearer ${token}` }
						: headers,
				credentials: 'same-origin',
				body: data,
				signal: params.signal,
			});
			if (response.status === 404) throw new Error(`${StatusHTTP.NOT_FOUND}`);
			if (response.status === 403) throw new Error(`${StatusHTTP.FORBIDDEN}`);
			if (!response.status) throw new Error(`${StatusHTTP.BAD_STATUS}`);
			const jsonResp = await response.json();
			//const bitrixSm =
			//	response.headers.get('set-cookie') &&
			//	response.headers.get('set-cookie').match(/BITRIX_SM_SALE_UID=([\d,a-z-A-Z]*)/)[1];

			return {
				success: true,
				data: jsonResp,
				//bitrixSm,
				status: StatusHTTP.OK,
			};
		} catch (e: any) {
			if (parseInt(e.message) === StatusHTTP.NOT_FOUND) {
				return {
					success: false,
					data: e,
					status: StatusHTTP.NOT_FOUND,
				};
			} else if (parseInt(e.message) === StatusHTTP.FORBIDDEN) {
				return {
					success: false,
					data: e,
					status: StatusHTTP.FORBIDDEN,
				};
			}
			return {
				success: false,
				data: e,
				status: StatusHTTP.BAD_STATUS,
			};
		}
	}
}
