import BannerNavApi from 'api/banner/BannerApi';
import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { LocationDataApi, LocationDataModel, normolizeLocationData } from '../models/LocationModel';

import { ApiResp, ILocationApi } from './types';

export default class LocationApi implements ILocationApi {
	private readonly _apiStore = RootStore.apiLocationStore;

	async getLocationData(): Promise<ApiResp<LocationDataModel>> {
		const response = await this._apiStore.request<ApiResponse<LocationDataApi, LocationDataApi>>({
			method: HTTPMethod.GET,
			endpoint: `/json?token=7a59a9acf2396b`,
		});
		try {
			response.data = normolizeLocationData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
