import { createSlice } from '@reduxjs/toolkit';
import { OrderProductItem } from 'api/order/models/orderList';
import { getOrderList, getOrdersMore, ordersReload } from './middleware';

type initialStateType = {
	orderList: OrderProductItem[];
	page: number;
	totalPages: number;
	ordersIsLoad: boolean;
};

const initialState: initialStateType = {
	orderList: [],
	page: 1,
	totalPages: 1,
	ordersIsLoad: true,
};

const OrderListStore = createSlice({
	name: 'orderList',
	initialState,
	reducers: {
		changeOrderList(state, action) {
			state.orderList = action.payload;
		},
		changePage(state, action) {
			state.page = action.payload;
		},
		changeTotalPages(state, action) {
			state.totalPages = action.payload;
		},
	},
	extraReducers(builder) {
		builder.addCase(getOrderList.pending, (state, action) => {
			state.ordersIsLoad = false;
		});
		builder.addCase(getOrderList.fulfilled, (state, action) => {
			state.ordersIsLoad = true;
			if (state.page !== 1) return;
			state.orderList = action.payload.data.orderList || [];
			state.totalPages = action.payload.data.totalPages || 0;
			state.page = action.payload.data.totalPages ? state.page + 1 : state.page;
		});
		builder.addCase(getOrdersMore.fulfilled, (state, action) => {
			state.orderList = state.orderList.concat(action.payload.data.orderList) || [];
			state.totalPages = action.payload.data.totalPages || 1;
			state.page = action.payload.data.totalPages ? state.page + 1 : state.page;
		});
		builder.addCase(ordersReload.fulfilled, (state, action) => {
			state.orderList = action.payload.data.orderList || [];
		});
	},
});

export const { changeOrderList, changePage, changeTotalPages } = OrderListStore.actions;

export default OrderListStore.reducer;
