import React from 'react';
import * as Sentry from '@sentry/nextjs';
import Content from 'components/UI/Content';
import Button from 'components/UI/Button';
import Link from 'next/link';
import routes from 'helpers/routes';

import { SuisseIntl } from 'helpers/utils/fonts';
import styles from 'styles/fourOhFour.module.scss';

class ErrorBoundary extends React.Component {
	constructor(props) {
		super(props);

		// Define a state variable to track whether is an error or not
		this.state = { hasError: false, error: null, errorInfo: null, eventId: null };
	}
	componentDidCatch(error, errorInfo) {
		// You can use your own error logging service here
		// eslint-disable-next-line
		console.log({ error, errorInfo });
		this.setState({
			hasError: true,
			error: error,
			errorInfo: errorInfo,
		});
		Sentry.withScope(scope => {
			scope.setExtras(errorInfo);
			const eventId = Sentry.captureException(error);
			this.setState({ eventId });
		});
	}
	render() {
		// Check if the error is thrown
		if (this.state.hasError) {
			// You can render any custom fallback UI
			return (
				<div className={styles.error500}>
					<Content className={styles.fourOhFour}>
						<>
							<h1 className={SuisseIntl.className}>500</h1>
							<p> ,  </p>
							<Button
								onClick={() => Sentry.showReportDialog({ eventId: this.state.eventId })}
								isActive
								isInvisible
								text="   "
							/>
							<a href={routes.home}>  </a>
						</>
					</Content>
				</div>
			);
		}

		// Return children components in case of no error

		return this.props.children;
	}
}

export default ErrorBoundary;
