import { createAsyncThunk } from '@reduxjs/toolkit';
import BreadcrumbsApi from 'api/breadcrumbs/Breadcrumbs';

const breadcrumbsApi = new BreadcrumbsApi();

export const getBreadcrumbsProduct = createAsyncThunk(
	'breadcrumbsProduct/getBreadcrumbsProduct',
	async (params: { id: number }) => {
		const response = breadcrumbsApi.getBreadcrumbsData({ productId: params.id });
		return response;
	}
);
