import React, { ReactNode, useCallback, useEffect } from 'react';
import Router from 'next/router';
import ym, { YMInitializer } from 'react-yandex-metrika';

export type WithYandexMetrikaProps = {
	children: ReactNode;
};

const enabled = process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID;

const WithYandexMetrika = (props: WithYandexMetrikaProps) => {
	const { children } = props;

	const hit = useCallback(
		url => {
			if (enabled) {
				ym('hit', url);
			} else {
				// eslint-disable-next-line
				console.log(`%c[YandexMetrika](HIT)`, `color: orange`, url);
			}
		},
		[enabled]
	);

	useEffect(() => {
		hit(window.location.pathname + window.location.search);
		Router.events.on('routeChangeComplete', (url: string) => hit(url));
	}, []);

	return (
		<>
			{enabled && (
				<YMInitializer
					accounts={[Number(process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID)]}
					options={{
						clickmap: true,
						trackLinks: true,
						accurateTrackBounce: true,
						webvisor: true,
						ecommerce: 'dataLayer',
						defer: true,
					}}
					version="2"
				/>
			)}
			{children}
		</>
	);
};

export default WithYandexMetrika;
