import { createSlice } from '@reduxjs/toolkit';
import { FavoritesModel } from 'api/favorites/models/favorites';
import { getFavoritesIds, getFavoritesList } from './middleware';

type initialStateType = {
	favoritesIds: number[];
	favoritesList: FavoritesModel;
};

const initialState: initialStateType = {
	favoritesIds: [],
	favoritesList: [],
};

const FavoritesStore = createSlice({
	name: 'favorites',
	initialState,
	reducers: {
		changeFavoritesIds(state, action) {
			state.favoritesIds = action.payload;
		},
	},
	extraReducers(builder) {
		builder.addCase(getFavoritesIds.fulfilled, (state, action) => {
			state.favoritesIds = action.payload.data ? action.payload.data.data : [];
		});
		builder.addCase(getFavoritesList.fulfilled, (state, action) => {
			state.favoritesList = action.payload.data;
		});
	},
});

export const { changeFavoritesIds } = FavoritesStore.actions;

export default FavoritesStore.reducer;
