import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { BreadcrumbsDataApi, BreadcrumbsDataModel, normolizeBreadcrumbsData } from '../models/BreadcrumbsModel';

import { ApiResp, BreadcrumbsParams, IBreadcrumbsApi } from './types';

export default class BreadcrumbsApi implements IBreadcrumbsApi {
	private readonly _apiStore = RootStore.apiStore;

	async getBreadcrumbsData(params: BreadcrumbsParams): Promise<ApiResp<BreadcrumbsDataModel>> {
		const response = await this._apiStore.request<ApiResponse<BreadcrumbsDataApi, BreadcrumbsDataApi>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/catalog/breadcrumbs?${params.productId ? 'productId=' + params.productId + '&' : ''}${
				params.catalogCode ? 'catalogCode=' + params.catalogCode : ''
			}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
		});
		try {
			response.data = normolizeBreadcrumbsData(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
